<template>
  <a class="up"
     @click="handleClick"></a>
</template>

<script>
export default {
  name: "Up",
  methods: {
    handleClick() {
      window.scrollTo(0, 0);
    }
  }
};
</script>

<style lang="scss">
@import "./../assets/style/var";

.up {
  position: fixed;
  right: $space;
  bottom: $space;
  border-radius: 50%;
  box-shadow: 0 1px 2px $--color-300;
  overflow: hidden;
  z-index: 1;

  &::after {
    display: block;
    content: "➜";
    width: 35px;
    height: 35px;
    line-height: 33px;
    text-align: center;
    background-color: #fff;
    transform: rotate(-90deg);
    transition: all 100ms ease-in-out;
  }

  &:hover::after {
    color: #fff;
    background-color: $--color-100;
    cursor: pointer;
  }
}
</style>
