<template>
  <ul v-if="tops.length > 1"
      class="location">
    <li :key="index"
        :class="{active: index === select}"
        v-for="(top, index) in tops"
        @click="handleClick(index, top)">
      {{index + 1}}
    </li>
  </ul>
</template>

<script>
import debounce from "@/utils/debounce";

const RANGE = 315;

export default {
  name: "Location",
  props: {
    tops: {
      type: Array,
      default: () => []
    }
  },
  data: function() {
    return {
      select: 0
    };
  },
  methods: {
    init() {
      const vm = this;

      window.addEventListener(
        "scroll",
        debounce(() => {
          const Y = document.documentElement.scrollTop;
          vm.select = vm.tops.findIndex(
            top => Y - RANGE < top && top < Y + RANGE
          );
        }, 500)
      );
    },
    handleClick(index, y = 0) {
      this.select = index;
      window.scrollTo(0, y);
    }
  },
  mounted: function() {
    this.init();
  },
  beforeDestroy: function() {
    window.removeEventListener("scroll");
  }
};
</script>

<style lang="scss">
@import "./../assets/style/var";

$w: 10px;

.location {
  position: fixed;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
  padding: $w 0;
  font-size: 12px;
  white-space: nowrap;
  z-index: 1;

  li {
    position: relative;
    display: inline-block;
    width: $w * 2;
    height: $w * 2;
    line-height: 18px;
    border-radius: 50%;
    background-color: #fff;
    text-align: center;
    box-shadow: 0 1px 2px $--color-300;
    cursor: pointer;
    transition: all 120ms ease-in-out;

    & + li {
      margin-left: $w;

      &::before {
        position: absolute;
        left: -$w;
        content: "";
        width: $w;
        height: 2px;
        margin-top: 9px;
        background-color: #fff;
        box-shadow: 0 1px 2px $--color-300;
        z-index: -1;
      }
    }

    &:hover {
      color: $--color-100;
      font-weight: 600;
    }

    &.active {
      color: #fff;
      background-color: $--color-100;
    }
  }
}
</style>
